ccount creation, registering thoughts, and to obtain the latest group, member, and stats data. The Express app utilizes the Tedious library in order to query the SQL Server database. Most of the app functionality is in the form of stored procedures which the Express app triggers. The iOS app takes care of basic front-end validation of user input, and most of the app's business logic is in SQL, with some additional filtering taking place on the Express server. One of the reasons I decided to take this approach was to make it easier to create the Android and web apps in the future, if we decide to do so. ")]),_c('p',[_vm._v(" This project is a collaboration with a good friend, and we have recently recruited a group of our mutual friends to take the app through its paces via Apple's Test Flight. We are currently ironing out various bugs and creating additional statistical queries and views in order to provide the user with more insight and make the app more interesting and fun to use. ")])])])],1),_c('v-expansion-panel',[_c('v-expansion-panel-header',[_c('span',[_c('strong',[_vm._v("Piano Notes Trainer: ")]),_vm._v(" Swift & Xcode, UIKit, AVFoundation; Sketch ")])]),_c('v-expansion-panel-content',[_c('div',{staticClass:"panelContent py-5 px-0"},[_c('div',{staticClass:"outerVideoContainer elevation-5"},[_c('span',{staticClass:"horizontalVideoContainer"},[_c('video',{staticClass:"block pianoAppVideo",attrs:{"width":"100%","max-height":"100%","autoplay":"","loop":"","muted":"muted","poster":"https://res.cloudinary.com/solbe/video/upload/v1607662549/matveycodes.com/piano_app_rn34bq.jpg","playsinline":""},domProps:{"muted":true}},[_c('source',{attrs:{"type":"video/mp4","src":"https://res.cloudinary.com/solbe/video/upload/v1607662549/matveycodes.com/piano_app_rn34bq.mp4"}}),_c('source',{attrs:{"type":"video/webm","src":"https://res.cloudinary.com/solbe/video/upload/v1607662549/matveycodes.com/piano_app_rn34bq.webm"}}),_vm._v(" Your browser does not support HTML5 video tag. "