blems to solve. It took some time to figure out how to best programmatically deal with enharmonic equivalents (e.g. F# & Gb), especially when octave changes are involved (e.g. B#3 == C4). I also decided to use two versions of the middle C, as well as C# & Cb, to help students deal with the confusion of switching clefs. ")]),_c('p',[_vm._v(" For game levels that are designed around the musical staff I had to use a different image in order to display a bigger range. ")]),_c('p',[_vm._v(" The note on staff moves up and down with a simple swiping/panning gesture with one finger. To add this functionality, I used the UIPanGestureRecognizer in combination with some basic, albeit slightly tricky algebra that calculates the current note to display by factoring in image height and finger position on the image view. ")]),_c('p',[_vm._v(" The last few weeks of the development process were mainly spent perfecting the logic of the multitude of options I decided to add to the settings menu. My goal was to enable students, teachers, and parents to set up the game to train for very specific goals, such as focusing on just the treble clef (or just the bass clef), or only practicing the sharps flats in a specific range. ")]),_c('p',[_vm._v(" App Store link: "),_c('a',{attrs:{"href":"https://apple.co/2NeIkm6"}},[_vm._v("https://apple.co/2NeIkm6")])])],1)])])],1),_c('v-expansion-panel',[_c('v-expansion-panel-header',[_c('span',[_c('strong',[_vm._v("Gemrover eBay Template & Website: ")]),_vm._v(" HTML, CSS, and lots of flexbox ")])]),_c('v-expansion-panel-content',[_c('div',{staticClass:"d-flex justify-space-between flex-wrap py-5 px-0",attrs:{"fill-height":""}},[_c('div',{staticClass:"panelContent py-5 px-0"},[_c('div',{staticClass:"contentBlock"},[_c('img',{staticClass:"projectImage float-sm-right rounded-lg elevation-5",staticStyle:{"max-height":"590px","width":"auto"},attrs:{"src":require('@/assets/img/gemrover_ebay_2.png'),"id":"gemroverTopImage"}}),_c('div',{},[_c('p',[_vm._v(" Ge